input {
    margin-top: 5px;
    border: none;
    font-size: 1rem;
    cursor: text;
    //font-family: "Inconsolata", "Monaco", "Consolas", "Lucida Console", monospace !important;
    font-family: "Avenir-light", "AvenirLTStd-Light", sans-serif !important;
}

.form-control::-moz-placeholder{
   @include placeholder($medium-gray,1);
}
.form-control:-moz-placeholder{
   @include placeholder($medium-gray,1);
}
.form-control::-webkit-input-placeholder{
   @include placeholder($medium-gray,1);
}
.form-control:-ms-input-placeholder{
   @include placeholder($medium-gray,1);
}

.form-control {
    font-family: "Avenir-light", "AvenirLTStd-Light", sans-serif !important;
    display: block;
    width: 100%;
    /*font-size: $font-size-base;*/
    line-height: 1.846;
    color: #666666;
    border: medium none;
    border-radius: $border-radius-extra-small;
    border-bottom: 2px solid #0f5b8c;
    vertical-align: middle;
    /*-webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);*/
    -webkit-transition: border-color ease-in-out .15s, -webkit-box-shadow ease-in-out .15s;
    -o-transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
    transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
    @include input-size($padding-small-vertical, $padding-small-horizontal, 30px);

    /*background-color: $gray-input-bg;
    border: medium none;
    border-radius: $border-radius-base;
    color: $font-color;
    font-size: $font-size-base;
    transition: background-color 0.3s ease 0s;
    @include input-size($padding-base-vertical, $padding-base-horizontal, $height-base);
    @include box-shadow(none);*/

    &:focus{
           background-color: $white-bg;
           //@include box-shadow(none);
           outline: 0 !important;
            border-bottom: 2px solid #2196f3;
            /*-webkit-box-shadow: inset 0 -2px 0 #2196f3;
            box-shadow: inset 0 -2px 0 #2196f3;*/
    }

    .has-success &,
    .has-error &,
    .has-success &:focus,
    .has-error &:focus{
        @include box-shadow(none);
    }

    .has-success &{
        background-color: $success-input-bg;
        color: $success-color;
        &.border-input{
             border: 1px solid $success-color;
        }
    }
    .has-success &:focus{
        background-color: $white-bg;
    }
    .has-error &{
        background-color: $danger-input-bg;
        color: $danger-color;
        &.border-input{
             border: 1px solid $danger-color;
        }
    }
    .has-error &:focus{
        background-color: $white-bg;
    }

    & + .form-control-feedback{
        border-radius: $border-radius-large;
        font-size: $font-size-base;
        margin-top: -7px;
        position: absolute;
        right: 10px;
        top: 50%;
        vertical-align: middle;
    }
    &.border-input{
         border: 1px solid $table-line-color;
    }
    .open &{
        border-bottom-color: transparent;
    }
}

.input-lg{
    height: 55px;
    padding: $padding-large-vertical $padding-large-horizontal;
    font-size: 17px;
    line-height: 1.3333333;
    border-radius: 3px;
}

.has-error{
    .form-control-feedback, .control-label{
        color: $danger-color;
    }
}
.has-success{
    .form-control-feedback, .control-label{
        color: $success-color;
    }
}


.input-group-addon {
    background-color: $gray-input-bg;
    border: medium none;
    border-radius: $border-radius-base;

    .has-success &,
    .has-error &{
        background-color: $white-color;
    }
    .has-error .form-control:focus + &{
        color: $danger-color;
    }
    .has-success .form-control:focus + &{
        color: $success-color;
    }
    .form-control:focus + &,
    .form-control:focus ~ &{
        background-color: $white-color;
    }
}
.border-input{
    .input-group-addon{
        border: solid 1px $table-line-color;
    }
}
.input-group{
    margin-bottom: 15px;
}
.input-group[disabled]{
    .input-group-addon{
        background-color: $light-gray;
    }
}
.input-group .form-control:first-child,
.input-group-addon:first-child,
.input-group-btn:first-child > .dropdown-toggle,
.input-group-btn:last-child > .btn:not(:last-child):not(.dropdown-toggle) {
    border-right: 0 none;
}
.input-group .form-control:last-child,
.input-group-addon:last-child,
.input-group-btn:last-child > .dropdown-toggle,
.input-group-btn:first-child > .btn:not(:first-child) {
    border-left: 0 none;
}
.form-control[disabled], .form-control[readonly], fieldset[disabled] .form-control {
    background-color: $light-gray;
    cursor: not-allowed;
    @include placeholder($dark-gray,1);
}
.form-control[disabled]::-moz-placeholder{
   @include placeholder($dark-gray,1);
}
.form-control[disabled]:-moz-placeholder{
   @include placeholder($medium-gray,1);
}
.form-control[disabled]::-webkit-input-placeholder{
   @include placeholder($medium-gray,1);
}
.form-control[disabled]:-ms-input-placeholder{
   @include placeholder($medium-gray,1);
}
.input-group-btn .btn{
    border-width: $border-thin;
    padding: $padding-round-vertical $padding-base-horizontal;
}
.input-group-btn .btn-default:not(.btn-fill){
    border-color: $medium-gray;
}

.input-group-btn:last-child > .btn{
    margin-left: 0;
}
textarea.form-control{
    max-width: 100%;
    padding: 10px 18px;
    resize: none;
}




